.main-header__link {
  width: 100%;
  height: 79px;
  display: flex;
  justify-content: center;
  fill: $header-logo-color;

  &:hover {
    fill: $header-logo-color-hover;
  }

  &:active {
    fill: $header-logo-color-active;
  }
}

.main-header__logo {
  margin: auto;
  fill: inherit;
}

.main-header__nav-toggle {
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  right: 0;
  display: flex;
  padding: 32px 31px 33px 31px;
  border: none;
  background: none;
  cursor: pointer;
  position: absolute;
}

.main-header__wrapper {
  display: flex;
  align-items: center;
  position: relative;
  box-shadow: $menu-shadow;
  z-index: 101;
}
